export interface TeamMember {
  id: string
  tenant_id: string
  name: string
  email?: string
  phone?: string
  role: 'owner' | 'admin' | 'member' | 'viewer'
  status: 'active' | 'inactive' | 'suspended'
  joined_at: string
  invited_by?: string
  company?: string
  inviter?: {
    name: string
  }
}

export interface TeamInvitation {
  id: string
  tenant_id: string
  inviter_id: string
  email: string
  role: 'owner' | 'admin' | 'member' | 'viewer'
  status: 'pending' | 'accepted' | 'declined' | 'expired'
  invitation_token: string
  expires_at: string
  created_at: string
  inviter_name?: string
  inviter?: {
    name: string
  }
}

export interface CreateInvitationData {
  email: string
  role: 'admin' | 'member' | 'viewer'
  message?: string
}

export interface AddExistingMemberData {
  user_id: string
  role: 'admin' | 'member' | 'viewer'
}

export interface UserSearchResult {
  id: string
  name: string
  email?: string
  company?: string
}

