import React, { useState, useEffect } from 'react'
import { Download, X, Monitor } from 'lucide-react'

interface BeforeInstallPromptEvent extends Event {
  readonly platforms: string[]
  readonly userChoice: Promise<{
    outcome: 'accepted' | 'dismissed'
    platform: string
  }>
  prompt(): Promise<void>
}

export const InstallPrompt: React.FC = () => {
  const [deferredPrompt, setDeferredPrompt] = useState<BeforeInstallPromptEvent | null>(null)
  const [showInstallPrompt, setShowInstallPrompt] = useState(false)
  const [isInstalled, setIsInstalled] = useState(false)

  useEffect(() => {
    // PWA    
    const handleBeforeInstallPrompt = (e: Event) => {
      e.preventDefault()
      setDeferredPrompt(e as BeforeInstallPromptEvent)
      setShowInstallPrompt(true)
    }

    // PWA    
    const handleAppInstalled = () => {
      setIsInstalled(true)
      setShowInstallPrompt(false)
      setDeferredPrompt(null)
    }

    //   
    const checkIfInstalled = () => {
      if (window.matchMedia && window.matchMedia('(display-mode: standalone)').matches) {
        setIsInstalled(true)
      }
    }

    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt)
    window.addEventListener('appinstalled', handleAppInstalled)
    checkIfInstalled()

    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt)
      window.removeEventListener('appinstalled', handleAppInstalled)
    }
  }, [])

  const handleInstallClick = async () => {
    if (!deferredPrompt) return

    try {
      await deferredPrompt.prompt()
      const { outcome } = await deferredPrompt.userChoice
      
      if (outcome === 'accepted') {
        console.log(' PWA  ')
        setShowInstallPrompt(false)
      } else {
        console.log(' PWA  ')
      }
      
      setDeferredPrompt(null)
    } catch (error) {
      console.error('PWA  :', error)
    }
  }

  const handleDismiss = () => {
    setShowInstallPrompt(false)
    // 24     ()
    localStorage.setItem('pwa-install-dismissed', Date.now().toString())
  }

  //      
  if (isInstalled || !showInstallPrompt || !deferredPrompt) {
    return null
  }

  return (
    <div className="fixed bottom-4 left-4 right-4 md:left-auto md:right-4 md:w-96 bg-white border border-gray-200 rounded-lg shadow-lg p-4 z-50">
      <div className="flex items-start space-x-3">
        <div className="flex-shrink-0">
          <div className="w-10 h-10 bg-blue-100 rounded-lg flex items-center justify-center">
            <Monitor className="w-5 h-5 text-blue-600" />
          </div>
        </div>
        
        <div className="flex-1 min-w-0">
          <h3 className="text-sm font-semibold text-gray-900 mb-1">
            PC  
          </h3>
          <p className="text-xs text-gray-600 mb-3">
              PC     .
          </p>
          
          <div className="flex items-center space-x-2">
            <button
              onClick={handleInstallClick}
              className="inline-flex items-center px-3 py-1.5 bg-blue-600 text-white text-xs font-medium rounded hover:bg-blue-700 transition-colors"
            >
              <Download className="w-3 h-3 mr-1" />
              
            </button>
            
            <button
              onClick={handleDismiss}
              className="inline-flex items-center px-3 py-1.5 bg-gray-100 text-gray-700 text-xs font-medium rounded hover:bg-gray-200 transition-colors"
            >
              
            </button>
          </div>
        </div>
        
        <button
          onClick={handleDismiss}
          className="flex-shrink-0 p-1 text-gray-400 hover:text-gray-600 transition-colors"
        >
          <X className="w-4 h-4" />
        </button>
      </div>
    </div>
  )
}

//     (  )
export const InstallButton: React.FC = () => {
  const [deferredPrompt, setDeferredPrompt] = useState<BeforeInstallPromptEvent | null>(null)
  const [isInstalled, setIsInstalled] = useState(false)
  const [isSupported, setIsSupported] = useState(false)

  useEffect(() => {
    const handleBeforeInstallPrompt = (e: Event) => {
      e.preventDefault()
      console.log(' PWA   ')
      setDeferredPrompt(e as BeforeInstallPromptEvent)
      setIsSupported(true)
    }

    const checkIfInstalled = () => {
      //    
      if (window.matchMedia && window.matchMedia('(display-mode: standalone)').matches) {
        console.log('  PWA   ')
        setIsInstalled(true)
        return
      }

      // iOS Safari    
      if ((window.navigator as any).standalone === true) {
        console.log(' iOS     ')
        setIsInstalled(true)
        return
      }

      //  PWA  
      if ('serviceWorker' in navigator && 'BeforeInstallPromptEvent' in window) {
        setIsSupported(true)
        console.log('  PWA ')
      } else {
        console.log('  PWA  ')
      }
    }

    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt)
    checkIfInstalled()

    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt)
    }
  }, [])

  const handleInstallClick = async () => {
    console.log('   ')
    
    if (!deferredPrompt) {
      console.log(' deferredPrompt  -   ')
      
      //   
      const isIOS = /iPad|iPhone|iPod/.test(navigator.userAgent)
      const isAndroid = /Android/.test(navigator.userAgent)
      
      if (isIOS) {
        alert('iOS Safari :\n\n1.   () \n2. "  " \n3. ""  ')
      } else if (isAndroid) {
        alert('Android Chrome :\n\n1.  () \n2. "  "  " " ')
      } else {
        alert('PC :\n\n1. Chrome/Edge     \n2.    " " ')
      }
      return
    }

    try {
      console.log(' PWA    ...')
      await deferredPrompt.prompt()
      const { outcome } = await deferredPrompt.userChoice
      
      console.log(`  : ${outcome}`)
      
      if (outcome === 'accepted') {
        console.log(' PWA  ')
        setIsInstalled(true)
        alert('  !   .')
      } else {
        console.log(' PWA  ')
      }
      
      setDeferredPrompt(null)
    } catch (error) {
      console.error(' PWA  :', error)
      alert('   .   " " .')
    }
  }

  //   
  if (isInstalled) {
    return (
      <div className="inline-flex items-center px-3 py-2 bg-green-100 text-green-700 text-sm font-medium rounded-lg">
        <span className="w-4 h-4 mr-2"></span>
         
      </div>
    )
  }

  // PWA   
  if (!isSupported && !deferredPrompt) {
    return (
      <button
        onClick={handleInstallClick}
        className="inline-flex items-center px-3 py-2 bg-gray-500 text-white text-sm font-medium rounded-lg hover:bg-gray-600 transition-colors"
        title="    "
      >
        <Download className="w-4 h-4 mr-2" />
          
      </button>
    )
  }

  return (
    <button
      onClick={handleInstallClick}
      className="inline-flex items-center px-3 py-2 bg-blue-600 text-white text-sm font-medium rounded-lg hover:bg-blue-700 transition-colors active:bg-blue-800"
      disabled={false}
    >
      <Download className="w-4 h-4 mr-2" />
      PC 
    </button>
  )
}