import React, { useState, useMemo, useEffect } from 'react'
import { useSearchParams } from 'react-router-dom'
import { Search, Plus, Grid, AlignLeft, Settings } from 'lucide-react'
import { Button, Card, Badge, Input, Select, Modal, Loading } from '@/components/ui'
import { PropertyCreateForm } from '@/components/forms/PropertyCreateForm'
import { PropertyEditForm } from '@/components/forms/PropertyEditForm'
import { PropertyCard } from '@/components/property/PropertyCard'
import { PropertyDetailModal } from '@/components/property/PropertyDetailModal'
import { useProperties } from '@/hooks/useProperties'
import { useTenant } from '@/contexts/TenantContext'
import { useAuth } from '@/contexts/AuthContext'
import { formatPrice } from '@/utils/propertyUtils'
import type { SimplePropertyFilters, Property, CreatePropertyData, UpdatePropertyData } from '@/types'

const PropertiesPageNew: React.FC = () => {
  
  const { user } = useAuth()
  const { tenant } = useTenant()
  const [searchParams, setSearchParams] = useSearchParams()
  const [searchTerm, setSearchTerm] = useState('')
  const [debouncedSearchTerm, setDebouncedSearchTerm] = useState('')
  const [isComposing, setIsComposing] = useState(false)
  const [selectedTransactionType, setSelectedTransactionType] = useState('')
  const [selectedPropertyType, setSelectedPropertyType] = useState('')
  const [viewMode, setViewMode] = useState<'card' | 'list'>('card')
  const [deleteConfirmOpen, setDeleteConfirmOpen] = useState(false)
  const [selectedProperty, setSelectedProperty] = useState<Property | null>(null)
  const [createFormOpen, setCreateFormOpen] = useState(false)
  const [createLoading, setCreateLoading] = useState(false)
  const [editFormOpen, setEditFormOpen] = useState(false)
  const [editLoading, setEditLoading] = useState(false)
  const [editingProperty, setEditingProperty] = useState<Property | null>(null)
  const [detailModalProperty, setDetailModalProperty] = useState<Property | null>(null)

  //  debounce  ( IME     )
  useEffect(() => {
    if (isComposing) return //    debounce  
    
    const timer = setTimeout(() => {
      setDebouncedSearchTerm(searchTerm)
    }, 300) // 300ms 

    return () => clearTimeout(timer)
  }, [searchTerm, isComposing])

  // URL       
  useEffect(() => {
    if (searchParams.get('create') === 'true') {
      setCreateFormOpen(true)
      // URL create  
      const newSearchParams = new URLSearchParams(searchParams)
      newSearchParams.delete('create')
      setSearchParams(newSearchParams, { replace: true })
    }
  }, [searchParams, setSearchParams])

  //    (debounced  )
  const filters = useMemo((): SimplePropertyFilters => {
    const result: SimplePropertyFilters = {}
    
    if (debouncedSearchTerm) result.search = debouncedSearchTerm
    if (selectedTransactionType !== '') result.transaction_type = selectedTransactionType
    if (selectedPropertyType !== '') result.property_type = selectedPropertyType
    
    return result
  }, [debouncedSearchTerm, selectedTransactionType, selectedPropertyType])

  const { 
    properties, 
    loading, 
    error, 
    refreshProperties,
    createProperty,
    updateProperty,
    deleteProperty 
  } = useProperties(filters)

  const transactionTypeOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
  ]

  const propertyTypeOptions = [
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
    { value: '', label: '' },
  ]

  //      



  const handleDeleteProperty = async () => {
    if (!selectedProperty) return
    
    try {
      await deleteProperty(selectedProperty.id)
      setDeleteConfirmOpen(false)
      setSelectedProperty(null)
    } catch (error) {
      console.error('Delete failed:', error)
    }
  }

  const resetFilters = () => {
    setSearchTerm('')
    setSelectedTransactionType('')
    setSelectedPropertyType('')
    //      
  }

  const handleCreateProperty = async (data: CreatePropertyData) => {
    console.log(' PropertiesPageNew.handleCreateProperty ')
    console.log('  :', data)
    //    
    if (import.meta.env.DEV && data.transaction_type === '') {
      console.log('Page  :', { price: data.price, type: typeof data.price })
    }
    console.log('  :', { user: user?.id, tenant: tenant?.id })
    
    try {
      console.log('   ...')
      setCreateLoading(true)
      
      console.log(' createProperty   ...')
      const result = await createProperty(data)
      console.log(' createProperty :', result)
      //    
      if (import.meta.env.DEV && result.transaction_type === '') {
        console.log('Page  :', { id: result.id, price: result.price })
      }
      
      console.log('   ...')
      //     
    } catch (error) {
      console.error(' PropertiesPageNew.handleCreateProperty :', error)
      console.error('  :', typeof error)
      console.error('  :', error instanceof Error ? {
        name: error.name,
        message: error.message,
        stack: error.stack
      } : error)
      throw error //   
    } finally {
      console.log(' PropertiesPageNew.handleCreateProperty ')
      setCreateLoading(false)
    }
  }

  const handleEditProperty = async (data: UpdatePropertyData) => {
    if (!editingProperty) return
    
    try {
      setEditLoading(true)
      await updateProperty(editingProperty.id, data)
      setEditFormOpen(false)
      setEditingProperty(null)
      //   
    } catch (error) {
      console.error('  :', error)
      throw error //   
    } finally {
      setEditLoading(false)
    }
  }

  const handleOpenEditForm = (property: Property) => {
    console.log('    :', property.title)
    try {
      setEditingProperty(property)
      setEditFormOpen(true)
      setDetailModalProperty(null) //   
      console.log('     ')
    } catch (error) {
      console.error('    :', error)
      alert('    .  .')
    }
  }

  const handleConfirmDelete = (property: Property) => {
    console.log('   :', property.title)
    try {
      const confirmDelete = window.confirm(`'${property.title}'   ?\n\n    .`)
      
      if (confirmDelete) {
        setSelectedProperty(property)
        setDeleteConfirmOpen(true)
        setDetailModalProperty(null) //   
        console.log('     ')
      } else {
        console.log('   ')
      }
    } catch (error) {
      console.error('   :', error)
      alert('    .  .')
    }
  }

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <Loading size="lg" text="  ..." />
      </div>
    )
  }

  if (error) {
    return (
      <div className="text-center py-12">
        <p className="text-red-600 mb-4">{error}</p>
        <Button onClick={refreshProperties}> </Button>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/*   */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between space-y-3 sm:space-y-0">
        <div className="flex-1">
          <h1 className="text-xl sm:text-2xl font-bold text-gray-900"> </h1>
          <p className="text-sm sm:text-base text-gray-600 mt-1">
            {tenant?.name}     <span className="font-semibold text-primary-600">{properties.length}</span> 
            {tenant?.limits.max_properties && (
              <span className="text-gray-500"> / {tenant.limits.max_properties} </span>
            )}
          </p>
        </div>
        <div className="flex sm:hidden"></div> {/*       */}
        <button 
          onClick={() => setCreateFormOpen(true)}
          className="hidden sm:inline-flex items-center px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors"
        >
          <Plus size={18} className="mr-2" />
           
        </button>
      </div>

      {/*     */}
      <Card className="p-4 sm:p-6">
        <div className="space-y-4">
          {/*  */}
          <div className="relative">
            <Input
              placeholder=",  ..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              onCompositionStart={() => setIsComposing(true)}
              onCompositionEnd={() => setIsComposing(false)}
              leftIcon={<Search size={18} />}
              className="text-base h-12"
            />
          </div>

          {/*   -   */}
          <div className="space-y-3">
            {/*   :  */}
            <div className="grid grid-cols-3 gap-2 sm:flex sm:items-center sm:space-x-3">
              {/*   */}
              <Select
                options={transactionTypeOptions}
                value={selectedTransactionType}
                onChange={(e) => setSelectedTransactionType(e.target.value)}
                className="w-full sm:w-24 text-sm"
              />

              {/*   */}
              <Select
                options={propertyTypeOptions}
                value={selectedPropertyType}
                onChange={(e) => setSelectedPropertyType(e.target.value)}
                className="w-full sm:w-24 text-sm"
              />

            </div>

            {/*   :    */}
            <div className="flex items-center justify-between">
              {/*   -   */}
              <div className="flex items-center bg-white rounded-lg p-1 border border-gray-300">
                <button
                  onClick={() => setViewMode('card')}
                  className={`flex items-center justify-center px-2 sm:px-3 py-2 rounded-md transition-colors text-xs sm:text-sm font-medium touch-target ${
                    viewMode === 'card' 
                      ? 'bg-primary-600 text-white' 
                      : 'text-gray-600 hover:text-gray-800 hover:bg-gray-50'
                  }`}
                >
                  <Grid size={14} className="sm:mr-1" />
                  <span className="hidden sm:inline"></span>
                </button>
                <button
                  onClick={() => setViewMode('list')}
                  className={`flex items-center justify-center px-2 sm:px-3 py-2 rounded-md transition-colors text-xs sm:text-sm font-medium touch-target ${
                    viewMode === 'list' 
                      ? 'bg-primary-600 text-white' 
                      : 'text-gray-600 hover:text-gray-800 hover:bg-gray-50'
                  }`}
                >
                  <AlignLeft size={14} className="sm:mr-1" />
                  <span className="hidden sm:inline"></span>
                </button>
              </div>

              {/*   -   */}
              <Button 
                variant="outline"
                onClick={resetFilters}
                size="sm"
                leftIcon={<Settings size={14} />}
                className="text-xs sm:text-sm px-3 py-2"
              >
                <span className="hidden sm:inline"></span>
                <span className="sm:hidden"></span>
              </Button>
            </div>
          </div>
        </div>
      </Card>

      {/*   */}
      {properties.length === 0 ? (
        <Card className="p-12 text-center">
          <div className="text-gray-400 mb-4">
            <Search size={48} className="mx-auto" />
          </div>
          <p className="text-gray-500">    .</p>
        </Card>
      ) : viewMode === 'card' ? (
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
          {properties.map(property => (
            <PropertyCard
              key={property.id}
              property={property}
              onClick={() => setDetailModalProperty(property)}
            />
          ))}
        </div>
      ) : (
        <PropertyList
          properties={properties}
          onView={(property) => setDetailModalProperty(property)}
        />
      )}

      {/*     */}
      <PropertyCreateForm
        isOpen={createFormOpen}
        onClose={() => setCreateFormOpen(false)}
        onSubmit={handleCreateProperty}
        loading={createLoading}
      />

      {/*     */}
      {detailModalProperty && (
        <PropertyDetailModal
          property={detailModalProperty}
          isOpen={true}
          onClose={() => {
            console.log('   ')
            setDetailModalProperty(null)
          }}
          onEdit={(property) => {
            console.log('    :', property.title)
            handleOpenEditForm(property)
          }}
          onDelete={(property) => {
            console.log('    :', property.title)
            handleConfirmDelete(property)
          }}
        />
      )}

      {/*     */}
      {editingProperty && (
        <PropertyEditForm
          isOpen={editFormOpen}
          onClose={() => {
            setEditFormOpen(false)
            setEditingProperty(null)
          }}
          onSubmit={handleEditProperty}
          property={editingProperty}
          loading={editLoading}
        />
      )}

      {/*    */}
      <Modal
        isOpen={deleteConfirmOpen}
        onClose={() => setDeleteConfirmOpen(false)}
        title=" "
      >
        <div className="space-y-4">
          <p className="text-gray-600">
            '{selectedProperty?.title}'  ?<br />
                .
          </p>
          <div className="flex justify-end space-x-3">
            <Button
              variant="outline"
              onClick={() => setDeleteConfirmOpen(false)}
            >
              
            </Button>
            <Button
              variant="destructive"
              onClick={handleDeleteProperty}
            >
              
            </Button>
          </div>
        </div>
      </Modal>
    </div>
  )
}


//   
interface PropertyListProps {
  properties: Property[]
  onView: (property: Property) => void
}

const PropertyList: React.FC<PropertyListProps> = ({ 
  properties, 
  onView
}) => {
  return (
    <Card>
      {/*   -   */}
      <div className="hidden lg:block bg-gray-50 border-b border-gray-200 px-4 py-3">
        <div className="grid grid-cols-12 gap-2 text-xs font-medium text-gray-700">
          <div className="col-span-1"></div>
          <div className="col-span-3"></div>
          <div className="col-span-3"></div>
          <div className="col-span-2"></div>
          <div className="col-span-1"></div>
          <div className="col-span-2"></div>
        </div>
      </div>
      
      {/*   */}
      <div>
        {properties.map(property => (
          <div 
            key={property.id} 
            className="border-b border-gray-100 hover:bg-blue-50 transition-colors cursor-pointer touch-target"
            onClick={() => onView(property)}
          >
            {/*   */}
            <div className="hidden lg:block px-4 py-3">
              <div className="grid grid-cols-12 gap-2 items-center text-sm">
                
                {/*  */}
                <div className="col-span-1">
                  <Badge size="sm" variant={
                    property.transaction_type === '' ? 'sale' : 
                    property.transaction_type === '' ? 'jeonse' : 'monthly'
                  }>
                    {property.transaction_type}
                  </Badge>
                  {/*      */}
                </div>
                
                {/*  */}
                <div className="col-span-3">
                  <div className="font-medium text-gray-900 truncate text-sm mb-1">
                    {property.title}
                  </div>
                  <div className="text-xs text-gray-600 truncate mb-1">
                     {property.address}
                  </div>
                  <div className="text-xs text-gray-500">
                    {property.type}  {property.area}m({Math.floor(property.area/3.3)})  {property.floor}/{property.total_floors}  {property.rooms} {property.bathrooms}
                  </div>
                </div>

                {/*  */}
                <div className="col-span-3">
                  <div className="font-bold text-gray-900 text-sm break-words">
                    {formatPrice(property)}
                  </div>
                  <div className="text-xs text-gray-500 mt-1">
                    : {property.created_at && new Date(property.created_at).toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' })}
                  </div>
                </div>

                {/*  */}
                <div className="col-span-2">
                  {property.landlord_name ? (
                    <div className="text-xs">
                      <div className="font-medium text-gray-700 truncate">{property.landlord_name}</div>
                      {property.landlord_phone && (
                        <div className="text-gray-500 truncate">{property.landlord_phone}</div>
                      )}
                    </div>
                  ) : (
                    <div className="text-xs text-gray-400"> </div>
                  )}
                </div>

                {/*  */}
                <div className="col-span-1">
                  {property.exit_date ? (
                    <div className="text-xs">
                      <div className="font-medium text-gray-700">
                        {new Date(property.exit_date).toLocaleDateString('ko-KR')}
                      </div>
                      <div className="text-gray-500"></div>
                    </div>
                  ) : (
                    <div className="text-xs text-gray-400"></div>
                  )}
                </div>

                {/*  () */}
                <div className="col-span-2">
                  <div className="flex items-center space-x-2 text-xs">
                    <span className={`${property.parking ? 'text-green-600' : 'text-gray-400'}`}>
                      {property.parking ? '' : 'X'}
                    </span>
                    <span className={`${property.elevator ? 'text-green-600' : 'text-gray-400'}`}>
                      {property.elevator ? '' : 'X'}
                    </span>
                  </div>
                  <div className="text-xs text-gray-500 mt-1">
                    : {property.view_count || 0}
                  </div>
                </div>

              </div>
            </div>

            {/*   */}
            <div className="lg:hidden px-4 py-4">
              <div className="space-y-3">
                {/*   : , ,  */}
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-2">
                    <Badge size="sm" variant={
                      property.transaction_type === '' ? 'sale' : 
                      property.transaction_type === '' ? 'jeonse' : 'monthly'
                    }>
                      {property.transaction_type}
                    </Badge>
                    {/*      */}
                  </div>
                  <div className="font-bold text-primary-600 text-base">
                    {formatPrice(property)}
                  </div>
                </div>
                
                {/*   :   */}
                <div className="font-medium text-gray-900 text-base">
                  {property.title}
                </div>
                
                {/*   :  */}
                <div className="text-sm text-gray-600">
                   {property.address}
                </div>
                
                {/*   :    */}
                <div className="text-sm text-gray-500">
                  {property.type}  {property.area}m ({Math.floor(property.area/3.3)})  {property.floor}/{property.total_floors}  {property.rooms} {property.bathrooms}
                </div>
                
                {/*   :   */}
                <div className="flex items-center justify-between text-xs text-gray-500">
                  <div className="flex items-center space-x-3">
                    <span className={`${property.parking ? 'text-green-600' : 'text-gray-400'}`}>
                      {property.parking ? '' : 'X'}
                    </span>
                    <span className={`${property.elevator ? 'text-green-600' : 'text-gray-400'}`}>
                      {property.elevator ? '' : 'X'}
                    </span>
                    {property.exit_date && (
                      <span className="text-orange-600">
                        : {new Date(property.exit_date).toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' })}
                      </span>
                    )}
                  </div>
                  <div>
                    : {property.created_at && new Date(property.created_at).toLocaleDateString('ko-KR', { month: 'short', day: 'numeric' })}
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </Card>
  )
}

export { PropertiesPageNew }