export const deleteProperty = async (propertyId: string, userId: string) => {
  
  try {
    //   tenant_id 
    const { data: userProfile, error: userError } = await supabase
      .from('user_profiles')
      .select('tenant_id')
      .eq('id', userId)
      .single()

    if (userError) {
      console.error('    :', userError)
      throw new Error('    .')
    }

    const actualTenantId = userProfile.tenant_id
    if (!actualTenantId) {
      throw new Error('   .')
    }

    //   ( tenant_id )
    const { error } = await supabase
      .from('properties')
      .delete()
      .eq('id', propertyId)
      .eq('tenant_id', actualTenantId) //  tenant_id 

    if (error) {
      console.error('   :', error)
      throw new Error(` : ${error.message}`)
    }

    return true
  } catch (error) {
    console.error(' deleteProperty  :', error)
    throw error
  }
}


//    (  )
export const getPropertyStats = async (userId: string) => {
  
  try {
    //   tenant_id 
    const { data: userProfile, error: userError } = await supabase
      .from('user_profiles')
      .select('tenant_id')
      .eq('id', userId)
      .single()

    if (userError) {
      console.error('    :', userError)
      throw new Error('    .')
    }

    const actualTenantId = userProfile.tenant_id
    if (!actualTenantId) {
      throw new Error('   .')
    }

    const { data, error } = await supabase
      .from('properties')
      .select('transaction_type, created_at')
      .eq('tenant_id', actualTenantId) //  tenant_id 

    if (error) {
      console.error('    :', error)
      throw error
    }


    const stats = {
      total: data.length,
      active: data.length, //    
      reserved: 0,
      sold: 0,
      this_month: data.filter(p => {
        const created = new Date(p.created_at)
        const now = new Date()
        return created.getMonth() === now.getMonth() && created.getFullYear() === now.getFullYear()
      }).length,
      by_transaction_type: {
        sale: data.filter(p => p.transaction_type === '').length,
        jeonse: data.filter(p => p.transaction_type === '').length,
        monthly: data.filter(p => p.transaction_type === '').length,
      }
    }

    return stats
  } catch (error) {
    console.error('Error in getPropertyStats:', error)
    throw error
  }
}


//   
export const togglePropertyFavorite = async (propertyId: string, userId: string) => {
  try {
    //   tenant_id 
    const { data: userProfile, error: userError } = await supabase
      .from('user_profiles')
      .select('tenant_id')
      .eq('id', userId)
      .single()

    if (userError) {
      console.error('    :', userError)
      throw new Error('    .')
    }

    const actualTenantId = userProfile.tenant_id
    if (!actualTenantId) {
      throw new Error('   .')
    }

    //     ( tenant_id )
    const { data: currentProperty } = await supabase
      .from('properties')
      .select('is_favorite')
      .eq('id', propertyId)
      .eq('tenant_id', actualTenantId) //  tenant_id 
      .single()

    if (!currentProperty) {
      throw new Error(ERROR_MESSAGES.PROPERTY_NOT_FOUND)
    }

    //    ( tenant_id )
    const { data, error } = await supabase
      .from('properties')
      .update({ is_favorite: !currentProperty.is_favorite })
      .eq('id', propertyId)
      .eq('tenant_id', actualTenantId) //  tenant_id 
      .select()
      .single()

    if (error) {
      console.error('Error toggling property favorite:', error)
      throw error
    }

    return data
  } catch (error) {
    console.error('Error in togglePropertyFavorite:', error)
    throw error
  }
}